a {border:0;background:transparent;margin:0;padding:0;font-style:normal;font-family:/*'Spoqa Han Sans',*/'Lato','Noto Sans KR','Noto Sans Korean','Noto Sans Korean','nanum gothic','Sans Serif';outline:0;}
html {font-size: 16px;height: 100%;}
body{line-height:1;height: 100%;}
ol,ul{list-style:none}
li{list-style:none;}
section, article, aside, hgroup, header, footer, nav, figure, figcaption, summary {display:block;}
address,caption,cite,code,dfn,em,th,var{font-style:normal;font-weight:normal;}
table {border-collapse:collapse; border-spacing:0;}
textarea {resize:none;}
em{font-style:normal;}
a {text-decoration: none}
a:hover{text-decoration:none;}
label {cursor:pointer; }
input,textarea{margin:0; vertical-align:bottom;overflow:visible;font-family: inherit;}
select {-webkit-appearance:none;}
html {overflow-y: auto;-webkit-text-size-adjust:none;}
button {cursor:pointer;font-size:inherit;border-radius: 0;}
img {border:0;}
.blind {display: none!important;}
.disno {display: none!important;}

